<template>
  <div>
    <div id="content" class="geotiff-viewer">
      <div id="form" >
      <h2>PARAM&Egrave;TRES ET RECHERCHE</h2>
        <div id="search" :class="disabled ? 'disabled' : ''">
          <div class="form-group">
               <h3 :class="extend0 ? 'extend' : ''" @click="extend0 = !extend0">BANDE / RASTER</h3>
               <div class="group-content">
                   <div class="input-group">
                       <select v-if="rasters.length>0" v-model="raster" @change="changeRaster">
                            <option v-for="raster in rasters" :value="raster.index">{{raster.text}}</option>
                       </select>
                       <div v-else>Aucun raster</div>
                   </div>
               </div>
          </div>
          <div class="form-group">
              <h3 :class="extend1 ? 'extend' : ''" @click="extend1 = !extend1">PARAM&Egrave;TRES DE L'IMAGE</h3>
              <div class="group-content">
                  <div class="input-group">
                    <h4>Opacit</h4>
                     <input type="range" min="0" max="1" step="0.1" v-model="opacity" @change="changeOpacity" :disabled="disabled"/>
                  </div>
                  <div class="input-group">
                    <h4>Echelle de couleurs</h4>
                     <select class="select-color-scale" v-model="colorScale" @change="changeColorScale" :disabled="disabled">
                      <option v-for="(item, index) in colorScales" :key="index">{{item}}</option>
                     </select>
                  </div>
                  <div class="input-group">
                    <h4>Minimum et maximum sur l'chelle de couleur</h4>
                    <formater-double-range ref="doubleRange" :disabled="disabled" :colorScale="colorScale" :width="250" :min="rangeScale.min" :max="rangeScale.max" :defaultRange="defaultRange" v-model="displayed" @change="changeMinMax"></formater-double-range>
                  </div>
                  <div class="input-group">
                    <h4>Valeurs  afficher</h4>
                    <div>
                        <input type="checkbox" v-model="clamp.low" @change="changeClamp" :disabled="disabled">
                        <label>Afficher les valeurs infrieures  {{displayed ? displayed.min.toPrecision(3) : defaultRange.min}}</label>
                    </div>
                    <div>
                        <input type="checkbox" v-model="clamp.high" @change="changeClamp" :disabled="disabled">
                        <label>Afficher les valeurs suprieures  {{displayed ? displayed.max.toPrecision(3) : defaultRange.max}}</label>
                    </div>
                  </div>
                  <div class="input-group">
                    <h4>Initialiser</h4>
                    <div>
                        <input type="button"  :disabled="disabled" @click="resetForm" value="Initialiser">
                    </div>
                  </div>
              </div>
         </div>
         <div class="form-group">
             <h3 :class="extend2 ? 'extend' : ''" @click="extend2 = !extend2">RECHERCHE</h3>
               <div class="group-content">
                 <div class="input-group">
                 <h4>Minimum et maximum</h4>
                 <input type="button" value="Recherche des optima" :disabled="(disabled || optima.min != null)" @click="searchOptima">
                 <ul>
                 <li><span class="label">Minimum</span>: {{(optima.min == null) ? '--' : optima.min | number}}</li>
                 <li><span class="label">Maximum</span>: {{(optima.max == null) ? '--' : optima.max | number}}</li>
                 </ul>
                 </div>
                 <div class="input-group">
                    <h4>Mode</h4>
                    <div>
                    <input type="radio" v-model="mode" value="point" :disabled="disabled" @change="changeMode"><label>Point</label>
                    <input type="radio" v-model="mode" value="profile" :disabled="disabled" @change="changeMode"><label>Profile</label>
                    </div>
                    <div class="instruction" v-if="mode == 'point'">Cliquez sur l'image afin d'obtenir la valeur en un point.</div>
                    <div class="instruction" v-else>Cliquer sur la carte, pour tracer un chemin.... @todo</div>
                 </div>
               </div>
            </div>
            <div class="form-group">
              <h3 :class="extend3 ? 'extend' : ''" @click="extend3 = !extend3">T&Eacute;L&Eacute;CHARGEMENT</h3>
               <div class="group-content">
                 <div class="input-group">
                 <h4>Tlcharger le fichier geotiff</h4>
                 <a :href="selectedFile" :disabled="disabled" download>Tlcharger</a>
                 </div>
                 <div class="input-group">
                    <h4>Publication</h4>
                    <div>
                    <input type="button"  value="Publier" disabled="disabled">
                    </div>
                 </div>
              </div>
            </div>
          </div>
      </div>
      <div id="mapTiff">
        <div class="map"></div>
        <div  v-show="hasChart" style="position:absolute;z-index:5000;width:100%; height:300px;bottom:0;left:0;text-align:center;">
        	<div id="chart" style="width:80%;margin:auto;padding:5px;background:white;"></div>
        </div> 
      </div>
      
    </div>
  </div>


</template>

<script>
/* eslint no-unused-vars: "off" */
 // import Datepicker from 'vuejs-datepicker'
  // require('../module/formater-config.js')
  import VueResource from 'vue-resource'
  import Vue from 'vue'
  var L = require('../module/leaflet.extends.js')
  import FormaterDoubleRange from './elements/formater-double-range'
  require('leaflet-draw')
  L.drawLocal = require('../module/leaflet.draw.js')

  var plotty = require('plotty')
  Vue.filter('number', function (value) {
    if (value.toFixed) {
      value = value.toFixed(2).toString().replace('.', ',').replace('-', '- ')
    }
    return value
  })

  let icon = new L.Icon({
    iconUrl: require('../assets/images/marker-icon.png'),
    iconRetinaUrl: require('../assets/images/marker-icon-2x.png'),
    shadowUrl: require('../assets/images/marker-shadow.png'),
    iconSize: [25, 41],
    iconAnchor: [12, 41],
    popupAnchor: [1, -34],
    tooltipAnchor: [16, -28],
    shadowSize: [41, 41]
  })
  var Highcharts = require('highcharts')

  Vue.use(VueResource)

  export default {
    name: 'GeotiffViewer',
    components: {
      VueResource,
      // Datepicker,
      FormaterDoubleRange
    },
    data () {
      return {
        extend0: false,
        extend1: false,
        extend2: false,
        extend3: false,
        disabled: true,
        map: null,
        renderer: null,
        geotiff: null,
        rasters: [{index: 0, text: 'Bande 1'}],
        raster: 0,
        interactiveImage: null,
//         zoom: 2,
//         center: [47.413220, -1.219482],
//         url: '//{s}.tile.opentopomap.org/{z}/{x}/{y}.png',
//         attribution: '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors',
        rangeScale: {
          min: -200,
          max: 200
        },
        defaultRange: {
          min: -50,
          max: 50
        },
        opacity: 0.7,
        displayed: null,
        displayedToStr: null,
        colorScale: 'rdbu',
        clamp: {
          low: true,
          high: true
        },
        optima: {
          min: null,
          max: null
        },
        mode: 'point',
        // @todo rcuprer les noms des fichiers
        // tiffs: ['https://api.poleterresolide.fr/geo_filt_20180508-20180520_sd_4rlks_small.tif'],
        tiffs: ['https://api.poleterresolide.fr/geotiff/geo_TOT_20170818.unw.tiff'],
        // @todo rcuprer le tableau colorScales de Plotty plotty/colorscales.js
        colorScales: Object.keys(plotty.colorscales).sort(),
        resizeListener: null,
        selectedFile: null,
        editableLayers: null,
        status: 'FREE',
        hasChart: false,
        chart: null
      }
    },
    mounted () {
      this.initDefault()
      this.initMap()
      this.selectedFile = this.tiffs[0]
      this.loadGeotiff(this.tiffs[0])
      window.scrollTo(0, 350)
      this.resizeListener = this.handleResize.bind(this)
      window.addEventListener('resize', this.resizeListener)
    },
    destroyed () {
      window.removeEventListener('resize', this.resizeListener)
      this.resizeListener = null
      this.editableLayers.eachLayer(function (layer) {
        layer.remove()
      })
      this.editableLayers = null
      this.drawControl.remove()
      this.drawControl = null
    },
    methods: {
      initDefault () {
        this.reset()
        this.defaultRange = {
          min: -50,
          max: 50
        }
        this.selectedFile = null
       // this.resetForm()
      },
      /* Initialise la carte
       */
      initMap: function () {
        var container = this.$el.querySelector('div.map')
        var _this = this
        this.map = L.map(container).setView([0, 0], 2)
        L.tileLayer('//server.arcgisonline.com/ArcGIS/rest/services/World_Topo_Map/MapServer/tile/{z}/{y}/{x}',
          {
            attribution: 'Tiles  <a href="https://services.arcgisonline.com/ArcGIS/rest/services/World_Topo_Map/MapServer">ArcGIS</a>',
            maxZoom: 18,
            minZoom: 2
          }).addTo(this.map)
        // this.profileLine = null
        this.editableLayers = new L.FeatureGroup()

        this.drawControl = new L.Control.Draw({
          draw: {
            polyline: {
              allowIntersection: false,
              shapeOptions: {
                color: '#FF0000',
                interactive: true
              },
              interactive: true
            },
            marker: false,
            circle: false,
            polygon: false,
            rectangle: false},
          edit: {
            featureGroup: _this.editableLayers
          }
        })
        var node = this.$el.querySelector('.leaflet-control-container .leaflet-top.leaflet-left')
        node.addEventListener('mouseover', function (e) {
          e.stopPropagation()
          console.log('mouseover')
        })
        this.map.on('draw:drawstart draw:editstart draw:deletestart', function (e) {
          _this.status = 'DRAWING'
          // _this.interactiveImage.bringToFront()
        })
        this.map.on('draw:drawstop draw:editstop draw:deletestop', function (e) {
          _this.status = 'FREE'
        })
        this.map.on('draw:created', function (e) {
          var layer = e.layer
          var path = layer.getLatLngs()
         // _this.pathTreatment(path)
          _this.editableLayers.addLayer(layer)
          layer.options.interactive = true
          layer.on('click', function (event) {
            console.log('click on layer')
            if (_this.status === 'FREE') {
              console.log('affiche le profile')
              var data = this.prepareData()
              _this.createChart(data)
            }
            console.log(event)
          })
          var marker = null
          var edges = layer.stitchEdges()
          var values = []
          var bounds = _this.geotiff.getBounds()
          edges.forEach(function (edge) {
            edge.forEach(function (latlng) {
              // on controle que bien dans les bounds, sinon on met 0 comme valeur
              if (bounds.contains([latlng.lat, latlng.lng])) {
                var value = _this.geotiff.getValueAtLatLng(latlng.lat, latlng.lng)
                latlng.value = value
              } else {
                latlng.value = 0
              }
              values.push(latlng)
             // marker = L.marker([latlng.lat, latlng.lng], {icon: icon})
             // marker.addTo(_this.map)
            })
          })
          e.layer.setValues(values)
          var data = e.layer.prepareData()
          _this.createChart(data)
        })
        this.map.on('draw:drawvertex', function (e) {
          console.log('editvertex impossible  stopper')
//           console.log(L.Draw)
//           console.log(e.target instanceof L.Path)
//           e.layers.eachLayer(function (layer) {
//             console.log(layer instanceof L.Polyline)
//             if (!_this.geotiff.getBounds().contains(layer.getLatLng())) {
//               console.log('hors les clous')
//               // e.layers.removeLayer(layer)
//             }
//           })
//          // console.log(e.layer.getLatLngs())
          return false
        })
        this.interactiveImage = L.imageOverlay(
          'https://api.poleterresolide.fr/images/transparent.png',
          [[0, 0], [0.02, 0.2]],
          {
            crossOrigin: true,
           // zIndex: 2000,
            interactive: true,
            bubblingMouseEvents: false,
            alt: 'mon geotiff'
          }).on('click', function (event) {
            console.log('click')
            _this.searchOnGeotiffLayer(event)
          })
        this.handleResize()
      },
      /**
      * Load the geotiff "filepath"  and add it to the map
      * With the colorScale in this.colorScale
      * displayMin
      * @param String filepath file url
      */
      loadGeotiff: function (filepath) {
        var options = {
          clampLow: this.clamp.low,
          clampHigh: this.clamp.high,
          colorScale: this.colorScale
        }
        var opacity = this.opacity
        this.renderer = L.LeafletGeotiff.plotty(options)
        this.geotiff = L.leafletGeotiff(filepath,
          {
            renderer: this.renderer,
            band: this.raster
           // interactive: true,
           // bubblingMouseEvents: false
          })
        var _this = this
        this.geotiff.once('load', function () {
          // ajoute un dlai mais je ne comprends pas pourquoi
          // peut-tre un conflit avec un autre
          // var next = function () { _this.initGeotiff() }
          // setTimeout(next, 1000)
          _this.initGeotiff()
        })
        this.geotiff.addTo(this.map)
        // this.renderer.setColorScale(this.colorScale)
      },
      createChart (data) {
        var container = this.$el.querySelector('#chart')
        var _this = this
        var formatter = function () {
          // rechercher la position pour la distance this.x
          var s = 'Distance: ' + this.x.toFixed(1) + 'km'
          s += '<br />Valeur: ' + this.y.toFixed(3)
          return s
        }
        this.chart = Highcharts.chart(container, {
          chart: {
            height: 200,
            marginBottom: 20
          },
          title: {
            text: 'Valeurs sur la ligne'
          },
          xAxis: {
            title: {
              enabled: true,
              text: 'distance'
            },
            labels: {
              format: '{value} km'
            },
            maxPadding: 0.05,
            howLastLabel: true
          },
          yAxis: {
            title: {
              text: 'Value'
            },
            labels: {
              format: '{value}'
            },
            lineWidth: 2
          },
          legend: {
            enabled: false
          },
          tooltip: {
            shared: true,
            formatter: formatter
           // headerFormat: '<b>{series.name}</b><br/>',
           // pointFormat: '{point.x} km: {point.y)}'
          },
          plotOptions: {
            spline: {
              marker: {
                enable: false
              }
            }
          },
          series: [{
            name: 'Valeur',
            data: data
          }]
        })
        this.hasChart = true
      },
      initGeotiff () {
        var bounds = this.geotiff.getBounds()
        var count = this.geotiff.getRastersCount()
        this.updateRasters(count)
        this.map.flyToBounds(bounds)
        // this.map.setCenter(bounds.getNorthEast())
        // this.map.setZoom(10)
        this.initDoubleRange()
        this.disabled = false
        this.geotiff.setOpacity(this.opacity)
        this.interactiveImage.addTo(this.map)
        this.setImagePosition(bounds)
      },
      updateRasters (count) {
        this.rasters = []
        for (var i = 0; i < count; i++) {
          this.rasters.push({index: i, text: 'Bande ' + (i + 1)})
        }
      },
      initDoubleRange () {
        // init bounds value
        if (this.geotiff) {
          this.rangeScale = this.geotiff.getRange()
        } else {
          this.rangeScale = {
            min: -200,
            max: 200
          }
        }
        this.defaultRange = {
          min: this.rangeScale.min + 0.3 * (this.rangeScale.max - this.rangeScale.min),
          max: this.rangeScale.max - 0.3 * (this.rangeScale.max - this.rangeScale.min)
        }
        this.displayed = this.defaultRange
        this.displayedToStr = this.displayed
      },
      setImagePosition: function (bounds) {
        this.interactiveImage.setBounds(bounds)
      },
      changeColorScale: function () {
        this.renderer.setColorScale(this.colorScale)
      },
      changeOpacity: function () {
        this.geotiff.setOpacity(this.opacity)
      },
      changeMinMax: function (event) {
        this.displayed = event.displayed
        this.displayedToStr = event.str
        this.renderer.setDisplayRange(this.displayed.min, this.displayed.max)
        // this.geotiff.options.renderer = this.renderer
      },
      changeMode (event) {
        console.log(this.mode)
        switch (this.mode) {
          case 'point':
            this.editableLayers.remove()
            this.drawControl.remove()
            // remove chart
            for (var i = 0; i < Highcharts.charts.length; i++) {
              if (typeof Highcharts.charts[i] !== 'undefined') {
                Highcharts.charts[i].destroy()
              }
            }
            this.chart = null
            this.hasChart = false
            break
          case 'profile':
            var map = this.map
            this.editableLayers.addTo(map)
            this.drawControl.addTo(map)
            // this.interactiveImage.bringToFront()
            break
        }
      },
      changeClamp: function () {
        this.renderer.setClamp(this.clamp.low, this.clamp.high)
      },
      changeRaster () {
        this.optima = {
          min: null,
          max: null
        }
        this.geotiff.setBand(this.raster, true)
        this.initDoubleRange()
      },
      handleResize () {
        var h = document.querySelector('#menu').offsetHeight + 5
        this.$el.querySelector('#content').style.height = (window.innerHeight - h) + 'px'
        if (this.map) {
          this.map.invalidateSize()
        }
      },
      resetForm () {
        this.colorScale = 'rdbu'
        this.initDoubleRange()
        this.displayed = null
        this.strDisplayed = null
        // this.$refs.doubleRange.reset()
        this.optima.min = null
        this.optima.max = null
        this.clamp = {
          low: true,
          high: true
        }
        this.opacity = 0.7
        if (this.renderer) {
          var options = {
            colorScale: this.colorScale,
            displayMin: this.defaultRange.min,
            displayMax: this.defaultRange.max,
            clampLow: this.clamp.low,
            clampHigh: this.clamp.high
          }
          this.renderer.setOptions(options)
          this.changeOpacity()
        }
      },
      reset () {
        if (this.geotiff) {
          this.renderer = null
          this.geotiff.remove()
          this.geotiff = null
          this.interactiveImage.remove()
          this.editableLayers.each(function (layer) {
            this.removeLayer(layer)
          })
        }
        this.resetForm()
        this.disabled = true
      },
      searchOnGeotiffLayer: function (event) {
        switch (this.mode) {
          case 'point':
            var value = this.geotiff.getValueAtLatLng(event.latlng.lat, event.latlng.lng)
            var popup = L.popup({closePopupOnClick: false})
              .setLatLng(event.latlng)
              .setContent('Valeur: ' + value.toFixed(2).toString().replace('.', ',').replace('-', '- '))
              .openOn(this.map)
            break
          case 'profile':
            break
        }
      },
      searchOptima: function () {
        this.optima = this.geotiff.getMinMaxValues()
      }
    }
  }
</script>
<style>
#mapTiff .leaflet-control-container .leaflet-top.leaflet-left{
    pointer-events:none;
}
#mapTiff .leaflet-container .leaflet-draw-section a{
    color: #fff;
    font-weight: 700;
}
#mapTiff .leaflet-draw-tooltip {
    display: none;
}
#mapTiff .leaflet-draw-actions li {
    display: block;
    margin: 0 0 1px 0;
    border-radius: 0;
}
#mapTiff .leaflet-draw-actions {
    background: #555;
    margin-left:5px;
    padding: 2px;
    -webkit-border-radius: 0 4px 4px 4px;
    border-radius: 0 4px 4px 4px;
}
#mapTiff .leaflet-draw-actions li:first-child a{
    -webkit-border-radius: 0 4px 0 0;
    border-radius: 0 4px 0 0;
}
#mapTiff .leaflet-draw-actions li:last-child a{
    -webkit-border-radius: 0 0px 4px 4px;
    border-radius: 0 0px 4px 4px;
}
</style>
<style scoped>
/*  @import "../../node_modules/leaflet-draw/dist/leaflet.draw.css";
  @import "../../node_modules/leaflet/dist/leaflet.css";*/
 #content{
    width: 100%;
    min-height:500px;
    display:block;
  }
 .geotiff-viewer #mapTiff {
    margin-left:365px;
    margin-right:10px;
    position:relative;
    height: 100%;
    box-sizing: border-box;
  }
  #mapTiff div.map {
    position: absolute;
    height:100%;
    width:100%;
  }

  #form {
    display: block;
    width: 20%;
    background-color: #f0f0f0;
    float: left;
    min-width:350px;
    width: 350px;
    height:100%;
    padding:0px 5px 10px 10px;
    margin-left: 8px;
    overflow:hidden;
  }
  #form h2 {
   border-bottom: 1px solid grey;
   margin: 0 -8px 0 -10px;
   padding: 25px 0 20px 10px;
   box-shadow: 0px 1px 5px #888888;
  }
  #search{
   max-height:90%;
   overflow-y:auto;
  }
  .input-group select,
  .input-group input[type="range"]{
   width: 250px;
   margin-left: 20px;
  }
 .input-group{
   margin: 20px 0 20px 5px;
   clear: both;
  }
 .input-group h4{
   margin-bottom:10px;
 }
 .form-group{
   border-bottom: 1px solid grey;
   padding-bottom: 15px;
 }
 span.label{
   font-weight: 500;
 }
 #search h3{
   cursor: pointer;
   color:#000;
   pointer-events:auto;
 }
 #search.disabled h3{
   pointer-events:none;
   color:grey;
 }
 h3::before{
   content: "\23F5";
   font-size:1.2em;
   padding-right: 5px;
 }
 h3.extend::before{
   content: "\23F7";
 }
 h3 + .group-content{
   max-height:0px;
   height:auto;
   overflow:hidden;
   transition: max-height .5s ease;
 }
 h3.extend + .group-content{
   max-height:600px;
   transition: max-height .5s ease;
 }
 div.instruction{
   padding: 5px 10px;
   line-height:1;
   color: grey;
 }

</style>




